.product-top {
  background-color: #FFFFFF; 
  color: #343330;
  padding: 1px;
  height: 325px;
}

.product-textdiv {
  font-size: 1.25em;
  width: 250px;
  text-align: center;
}

.product-imgdiv {
  height: 175px;
  width: 250px;
}

.product-img {
  height: 175px;
  width: 175px;
}

.product-bottom {
  color: #000000; 
  background-color: #FFFFFF;
  padding: 1px;
}

.cart {
  background-color: #FFFFFF; 
  color: #343330;
  width: 1000px;
  min-height: 100px;
  margin: 75px auto 75px auto;
  padding: 35px;
  font-size: calc(10px + 1vmin);
}

