input[type=text], input[type=email], input[type=number], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.table-cart {
  border-spacing: 10px;
}

.table-user {
  border-spacing: 10px;
}

.cart-img {
  height: 110px;
  width: 160px;
}